















































import beautyServiceTime from '../../components/beauty-service-time.vue';

export default {
	components:{
		beautyServiceTime,
	},
	data(){
		return{
			baseUrl:this.api.baseUrl,
			id:'',
			detail:{},
			
		};
	},
	onLoad(options) {
		this.id = options.id;
		this.loadData();
	},
	methods:{
		back(){
			uni.navigateBack();
			
		},
		loadData(){
			uni.request({
				url:this.api.showBeauticianl,
				data:{
					id:this.id,
					
				},
				success: (res) =>  {
					console.log(res.data);
					var result = res.data;
					if(result){
						this.detail = result.beauticianl;
					}else{
						uni.showToast({
							icon:"none",
							title:'数据异常'
						})
					}
				},
				fail:(error)=> {
					console.log("error :",error.errMsg);
					uni.showToast({
						icon:"none",
						title:'网络异常'
					})
				}
			})
			
		}
	}
	
	
}


